.icon-tel {
  color: currentColor;
  --btn-tel-bg: #fff;

  width: 2.8rem;
  height: 2.8rem;
}

.icon-shild {
  width: 5.5rem;
  height: 6.2rem;
}

#icon-shild {
  transition: @s;
  .icon-shild-bg {
    transition: inherit;
  }
  &:hover {
    .icon-shild-bg {
      transform: translate(-0.5rem, -0.5rem);
    }
  }
}

.icon-download {
  width: 1.8rem;
  height: 1.8rem;
}

.icon-triangle-bottom {
  position: absolute;
  z-index: 1;
  top: 50%;
  transform: translateY(-50%);
  right: 0;

  width: 1.6rem;
  height: 1.6rem;
  fill: @light-gray;

  transition: @s;
}

.active .icon-triangle-bottom,
.active.icon-triangle-bottom {
  transform: translateY(-50%) rotate(180deg);
}

@media (max-width: @tablet-width) {
  .icon-arrow-short {
    width: 1.1rem;
    height: 1.2rem;
  }

  .icon-region-2 {
    width: 2.2rem;
    height: 2.8rem;
  }

  .icon-compare {
    width: 1rem;
    height: 1rem;
  }

  .icon-shild {
